<template>
  <section class="bed-page">
    <div class="box-text">
      <h3>NumPy中dot方法矩阵积算法解析</h3>
      <p>
        在NumPy中，有一个内置方法<span class="code">numpy.dot</span>，使用方法如下：
      </p>
      <pre class="code-block">
  >>> a = np.array([[1, 1], [0, 1]])
  >>> b = np.array([[2, 0], [3, 4]])
  >>> np.dot(a, b)
      array([[5, 4],
             [3, 4]])
  >>> np.dot(b, a)
      array([[2, 2],
             [3, 7]])
      </pre>
      <p>
        究竟多维数组是如何运算得出最终的结果的？<strong>第R行第C列的结果 = 第一个矩阵的R行所有数据 * 第二个矩阵的C列所有数据 后的所有数据之和</strong>
      </p>
      <p>
        具体解释一下，例如上面的例子中，我们先把多维数组用矩阵的形式表现出来，假设我们需要计算出来的结果是c
      </p>
      <pre class="code-block">
  a               b                c
  1, 1            2, 0             ?1, ?2
  0, 1            3, 4             ?3, ?4
      </pre>
      <p>
        按照上面的规则来试着计算一下，?1是第1行第1列，所以?1的结果应该是a的第一行所有数据，即1, 1分别乘以b的第一列的所有数据，即2, 3之后的总和。那么最终结果是<strong>?1 = 1 * 2 + 1 * 3 = 5</strong>
      </p>
      <p>
        同理，<strong>?2 = 1 * 0 + 1 * 4 = 4</strong>，<strong>?3 = 0 * 2 + 1 * 3 = 3</strong>，<strong>?4 = 0 * 0 + 1 * 4 = 4</strong>。即最终的结果就是5, 4, 3, 4。另外一个矩阵积运算是同样的操作方式。
      </p>
    </div>
    <div class="box-tip">
      <p>2018-05-22</p>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'ai1',
