<template>
  <section class="bed-test">
    <div class="box-section">
      <div class="header">
        测试ajax
      </div>
      <div class="content">
        <div class="item">测试接口url：{{ ajaxTest.url }}</div>
        <div class="item">
          <div class="btn-sm" @click="testAjax">
            测 试
          </div>
        </div>
        <hr>
        <div class="item">
          Status：{{ ajaxTest.status }}
          Data: {{ ajaxTest.dataStr }}
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'test',
    data () {
      return {
        ajaxTest: {
          url: 'https://easy-mock.com/mock/5ae189c8a9fe34094ed54d32/example/mock',
          status: '',
          dataStr: ''
        }
      }
    },

    methods: {
      testAjax () {
        axios({
          method: 'get',
          url: this.ajaxTest.url
        }).then(response => {
          console.log(response)
          this.ajaxTest = Object.assign({}, this.ajaxTest, {
            status: response.status,
            dataStr: JSON.stringify(response.data)
          })
        }).catch(error => {
          console.error(error)
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .bed-test {
    padding: 12px 24px;
    .box-section {
      border: 1px solid rgb(136, 154, 207);
      border-radius: 5px;
      overflow: hidden;
      .header {
        height: 30px;
        line-height: 30px;
        padding: 0 6px;
        // border-bottom: 1px solid rgb(54, 87, 206);
        color: #fff;
        background-color: rgb(54, 87, 206);
      }
      .content {
        .item {
          min-height: 32px;
          max-height: 96px;
          line-height: 32px;
          padding: 0 6px;
          overflow: hidden;
          // text-overflow: ellipsis;
          // white-space: nowrap;
        }
      }
    }
  }
</style>



