import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App'

Vue.use(VueRouter)

import home from './components/home'
import pageCover from './pages/pageCover'
import pageTest from './pages/pageTest'
import pageAi0 from './pages/pageAi0'
import pageAi1 from './pages/pageAi1'
import pageMajiongEmpire from './pages/pageMajiongEmpire'
import PageWorkitCompare from './pages/PageWorkitCompare'
import page404 from './pages/page404'

const routes = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    path: '/workitcompare',
    name: 'workitCompare',
    component: PageWorkitCompare
  },
  {
    path: '/',
    component: home,
    children: [
      {
        path: '/index',
        name: 'index',
        component: pageCover
      }, // 入口页
      {
        path: '/ai0',
        name: 'ai0',
        component: pageAi0
      },
      {
        path: '/ai1',
        name: 'ai1',
        component: pageAi1
      },
      {
        path: '/gameMajiongEmpire',
        name: 'gameMajiongEmpire',
        component: pageMajiongEmpire
      },
      {
        path: '/test',
        name: 'test',
        component: pageTest
      }
    ]
  },
  {
    path: '*',
    redirect: {
      path: '/404'
    }
  },
  {
    path: '/404',
    component: page404
  }
]

const router = new VueRouter({
  routes
})

router.afterEach((to, from) => {
  // console.log(to, from)
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router
})



